import {
  createRouter,
  createWebHashHistory,
  createWebHistory,
  RouteRecordRaw,
} from "vue-router";
import i18n, { Locales } from "@/models/i18n";
import HomePage from "@/views/pages/HomePage.vue";
import ArticlePage from "@/views/pages/ArticlePage.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    alias: ["/about", "/product", "/service", "/news", "/contact"],
    name: "home",
    component: HomePage,
  },
  {
    path: "/article/:id",
    name: "article",
    component: ArticlePage,
  },
];

const hashMode = true;
const router = createRouter({
  history: hashMode ? createWebHashHistory() : createWebHistory(),
  routes,
});

window.routes = ["/article"];

router.beforeEach((to) => {
  if (to.query.lang) {
    i18n.global.locale = to.query.lang as Locales;
    localStorage.setItem("lang", i18n.global.locale);
  } else {
    const defaultLang = /^zh/.test(navigator.language) ? "zht" : "en";
    i18n.global.locale =
      (localStorage.getItem("lang") as Locales) || defaultLang;
  }
});
router.afterEach(() => {
  if (sessionStorage.getItem("site-loaded")) sessionStorage.clear();
  else {
    sessionStorage.setItem("site-loaded", "true");
    window.location.reload();
  }
});

export default router;
